import React, { Component } from 'react'
import { Card, CardTitle, CardBody, CardImg, CardImgOverlay,Button ,CardText} from 'reactstrap';
import Comments from './Comments'
import CommentForm from './CommentForm'

export class DishDetails extends Component {
    render() {
        
        const comment=this.props.comments.map((com)=>{
            // console.log('com:',com)
            return(
                <Comments comments={com} key={com.id} />
            )
           
        })
        // console.log('Map dishDetails:',comment)
        return (
            <div>
                <Card>
        <CardImg top width="100%" src={'http://localhost:3001/'+this.props.detail.image} alt={this.props.detail.name} />
        <CardBody>
          <CardTitle tag="h5" style={{textAlign:"center",marginBottom:"10px"}} >{this.props.detail.name}</CardTitle>
          <CardText tag='h6'>{this.props.detail.category} </CardText>
          <CardText tag='p'>{this.props.detail.description} </CardText>
          <CardText tag='h6'>Price :{this.props.detail.price} /- </CardText>
          <hr/>
          {comment}
          <hr/>
          <CommentForm dishId={this.props.detail.id} />
        </CardBody>
      </Card>
            </div>
        )
    }
}

export default DishDetails
