import React, { Component } from 'react'
import { Card, CardTitle, CardBody, CardImg, CardImgOverlay,Col } from 'reactstrap';


export class MenuItem extends Component {
    render() {
        // console.log(this.props)
        return (
            <Col sm='6' md='3' >
                <Card className='mt-4' >
                    <CardBody onClick={this.props.toggle}>
                        <CardImg width="100%"  src={'http://localhost:3001/'+this.props.image} alt="Card image cap" style={{opacity:"0.5"}} />
                        <CardImgOverlay>
                        <CardTitle tag="h5">{this.props.name}</CardTitle>
                        
                        </CardImgOverlay>
                    </CardBody>
                </Card>
            </Col>
        )
    }
}

export default MenuItem
