import React, { Component } from 'react'
import { Card, CardTitle, CardBody, CardImg, CardImgOverlay,Button ,CardText} from 'reactstrap';
import dateFormat from 'dateformat'


export class Comments extends Component {
    render() {
        
        // console.log('Comment.js:',this.props.comments)
        return(
           
            (
                <div>
                    <h5>{this.props.comments.author} </h5>
                    <p>Rating: {this.props.comments.rating} Star </p>
                    <p>{this.props.comments.comment} </p>
                    <p>{dateFormat(this.props.comments.date, "dddd, mmmm dS, yyyy, h:MM:ss TT")} </p>
                </div>
            )
                 
            
        )
    }
}

export default Comments
