import { actions } from "react-redux-form"

const initialize={
    dishes:null,
    comments:null
}

export const Reducer=(state=initialize,actions)=>{
    // console.log(actions)
    if(actions.type === 'DISHES'){
        return({
            ...state,
            dishes:actions.payload
        })
    }

    if(actions.type === 'COMMENTS'){
        return({
            ...state,
            comments:actions.payload
        })
    }
    if(actions.type === "ADD_COMMENT"){
        let com =actions.payload;
        com.id=state.comments.length;

        return({
            ...state,
            comments:state.comments.concat(com)
        })
    }
    return state ;
}

