import React, { Component } from 'react'
import { Button, Form, FormGroup, Label, Input, FormText } from 'reactstrap';
import {connect} from 'react-redux'
import axios from 'axios'

const mapDispatchToProps =(dispatch)=>{
    return({
        addComment:(name,rating,comment,date,dishId)=>{
            console.log('commentForm Date:',date)
            const newComment={
                author:name,
                rating:rating,
                comment:comment,
                date:date,
                dishId:dishId
            }

            axios.post('http://localhost:3001/comments',newComment)
            .then(response => response.data)
            .then(data=>{
                return(dispatch({
                    type:'ADD_COMMENT',
                    payload:newComment
                }))
            })
            
            
        }
    })
}

export class CommentForm extends Component {
    state={
        name:'',
        rating:'1',
        comment:''
    }
    handleInput=(e)=>{
        this.setState({
            [e.target.name]:e.target.value
        })
    }
    
    handleSubmit=(e)=>{
        e.preventDefault();
        const dishId=this.props.dishId
        const date=new Date();
        
        console.log('Submited date:',date)
        this.props.addComment(this.state.name,this.state.rating,this.state.comment,date,dishId)

        this.setState({
            name:'',
        rating:'1',
        comment:''
        })
    }

    render() {
        console.log(this.state)
        return (
            <Form onSubmit={this.handleSubmit} >
                <FormGroup>
                    <Label for="name">Name</Label>
                    <Input type="text" name="name" placeholder="Enter Your Name" onChange={(e)=>this.handleInput(e)} value={this.state.name} />
                </FormGroup><br/>
                
                <FormGroup>
                    <Label for="rating">Rating</Label>
                    <Input type="select" name="rating" onChange={(e)=>this.handleInput(e)} value={this.state.rating} >
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                    </Input>
                </FormGroup> <br/>
                <FormGroup>
                    <Label for="comment">Comment</Label>
                    <Input type="textarea" name="comment" placeholder="Enter Your Comment" onChange={(e)=>this.handleInput(e)} value={this.state.comment} />
                </FormGroup><br/>
                <Button type='submit' >Submit</Button>
                
            </Form>
        )
    }
}

export default connect(null,mapDispatchToProps) (CommentForm)
