import React from 'react';
import {Collapse, Navbar, NavbarToggler, NavbarBrand, Nav,Dropdown, DropdownItem, DropdownToggle, DropdownMenu, NavItem} from 'reactstrap'
import {Link} from 'react-router-dom'

class Navigation extends React.Component{
    state={
        isNavOpen:false,
        dropdownOpen:false
    }
    toggle=()=>{
        this.setState({
            isNavOpen: !this.state.isNavOpen
        })
    }

    dropdownToggle=()=>{
        this.setState({
            dropdownOpen:!this.state.dropdownOpen
        })
    }

    render(){
        
        return(
            <Navbar color="dark" dark expand="md">
                <div className="container">
                    
                     <NavbarBrand href="/">Restaurant</NavbarBrand>
                     <NavbarToggler onClick={this.toggle}/>
                     <Collapse isOpen={this.state.isNavOpen} navbar>
                     <Nav style={{marginLeft:'auto'}} navbar>
                            <NavItem>
                                <Link to='/' className='nav-link active'>Home</Link>
                            </NavItem>
                            <NavItem>
                                <Link to='/menu' className='nav-link'>All Menu Item</Link>
                            </NavItem>
                            <Dropdown nav isOpen={this.state.dropdownOpen} toggle={this.dropdownToggle}>
                            <DropdownToggle nav caret  >
                                Menu
                            </DropdownToggle>
                            <DropdownMenu  >
                                <DropdownItem tag={Link} to="/menu/meal">Meal</DropdownItem>
                                <DropdownItem tag={Link} to="/menu/snacks">Snacks</DropdownItem>
                                <DropdownItem tag={Link} to="/menu/appetizer">Appetizer</DropdownItem>
                                <DropdownItem tag={Link} to="/menu/desserts">Desserts</DropdownItem>
                            </DropdownMenu>
                            </Dropdown>
                            <NavItem>
                                <Link to='/about' className='nav-link'>About</Link>
                            </NavItem>
                            
                        </Nav>
                     </Collapse>
     
                    
                     
                </div>
            </Navbar>
        )
    }
}

export default Navigation;

