import React, { Component } from 'react'
import { connect } from 'react-redux'
import axios from 'axios'
import MenuItem from './MenuItem'
import { Card, CardTitle, CardBody, CardImg, CardImgOverlay,Row, CardColumns } from 'reactstrap';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import DishDetails from './DishDetails'
import Loading from './Loading'
import {BrowserRouter as Router,Switch,Route} from "react-router-dom";
import Meal from './Meal'

const mapStateToProps = (state) => {
    return ({
        dishes: state.dishes,
        comments: state.comments
    })
}
const mapDispatchToProps = (dispatch) => {
    return {
        fetchdishes: () => {
            axios.get('http://localhost:3001/dishes')
                .then(response => response.data)
                .then(data => {
                    dispatch({
                        type: "DISHES",
                        payload: data
                    })
                })
        },
        fetchcomments: () => {
            axios.get('http://localhost:3001/comments')
                .then(response => response.data)
                .then(data => {
                    dispatch({
                        type: "COMMENTS",
                        payload: data
                    })
                })
        }
    }
}



export class Snacks extends Component {

    state={
        isOpen:false,
        selectDish:null
    }

    componentDidMount() {
        this.props.fetchdishes()
        this.props.fetchcomments()
    }
    
    toggle=()=>{
       
        this.setState({
            isOpen: !this.state.isOpen,
            selectDish:null
        })
    }

    to=(i)=>{
        // console.log('dishId',i)
        this.setState({
            isOpen: !this.state.isOpen,
            selectDish:i
        })
    }



    render() {

        
       
       
        if (this.props.dishes === null) {
            
            return(
               <Loading/>
            )
        }
        else {

      const snacks=this.props.dishes.filter((dish)=>{
          return(dish.category === 'snacks')
      })
      
      
      const snack = snacks.map((item) => {
        // console.log(item)
        return (
          <MenuItem dishId={item.id} name={item.name} image={item.image} price={item.price} key={item.id} 
          toggle={()=>this.to(item)}
                />  
        )
    })
            let id;
            let detail;

            if(this.state.selectDish !== null){
                // console.log('SelectDish',this.state.selectDish.id)
                id = this.state.selectDish.id
                const comments=this.props.comments.filter((com)=>{
                    return(id === com.dishId)
                })
                // console.log('Filtered comments',comments)
                detail=<DishDetails detail={this.state.selectDish} comments={comments} />
                
            }

            const closeBtn = <button className="close" onClick={this.toggle} style={{textAlign:'right' }} >&times;</button>;

            return (
                <div className='container'>
                    
                    <div className='mt-4'>
                      <h3>Snacks</h3>
                      <Row>
                        {snack}
                      </Row>
                    </div>
                    
                    <Modal isOpen={this.state.isOpen}  >
                    <ModalHeader toggle={this.toggle} close={closeBtn}>Dish Details</ModalHeader>
                            <ModalBody>
                               {detail}
                            </ModalBody>
                            <ModalFooter>
                            <Button color="secondary" onClick={this.toggle}>Cancel</Button>
                            </ModalFooter>
                        </Modal>
    
                </div>
            )
        }

       


        

       

        
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Snacks)

